body {
  background-color: black;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

canvas {
  position: absolute;
  top: 30px;
  left: 30px;
  width: 500px;
  height: 500px;
  margin: 20px;
  border: 1px solid black;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -webkit-optimize-contrast;
      image-rendering: -moz-crisp-edges;
      image-rendering: -o-pixelated;
      image-rendering: pixelated;
}

ul {
  list-style-type: none;
  position: absolute;
  color: white;
  font: arial;
  font-size: 20px;
}

message {
  position: absolute;
  color: white;
  top: 20px;
  left: 50px;
  font: arial;
  font-size: 20px;
}

p {
  position: absolute;
  word-wrap: break-word;
  color: white;
  top: 560px;
  left: 50px;
  width: 400px;
  font: arial;
  font-size: 20px;
}

#monster {
  top: 150px;
  left: 530px;
}

#player {
  top: 50px;
  left: 530px;
}



